import React, { Component } from 'react'
import TextareaAutosize from 'react-autosize-textarea'
import mods from './mods'

class App extends Component {
  constructor () {
    super()

    this.state = {
      itemDesc: '',
      league: 'Bestiary',
      resultList: null,
      loadingList: false,
      errorList: null,
      activeItemId: '',
      mods: mods.mods,
    }
  }

  mapResultList = (list) => {
    if (this.state.loadingList)
      return (
        <div>
          <img className='img-circle rotating' alt='Billy'
               src='data:image/jpeg;base64,'/>
        </div>
      )
    else if (this.state.errorList)
      return (
        <div>
          ERROR
          {this.state.errorList}
        </div>
      )
    else if (!list)
      return (
        <span/>
      )
    else if (list.length <= 0)
      return (
        <h4>No results</h4>
      )
    else {
      return (
        <div>
          <h3>Best results:</h3>
          <ul className='list-group scroll'>
            {list.map(item => {
              return (
                <div key={item.id}>
                  <li className='list-group-item' onClick={() => {
                    if (this.state.activeItemId === item.id)
                      this.setState({
                        ...this.state,
                        activeItemId: '',
                      })
                    else
                      this.setState({
                        ...this.state,
                        activeItemId: item.id,
                      })
                  }}>{item.info.price.amount} - {item.info.price.currency}</li>
                  {this.state.activeItemId === item.id && item.item.explicitMods.map(mod => {
                    return (
                      <li key={mod}>{mod}</li>
                    )
                  })}
                </div>
              )
            })}
          </ul>
        </div>
      )
    }
  }

  onChange = (e) => {
    const state = this.state
    state[e.target.name] = e.target.value
    this.setState(state)
  }

  onSubmit = (e) => {
    e.preventDefault()

    let itemDesc = this.state.itemDesc, itemName, itemType, itemMods

    this.setState({...this.state, loadingList: true, resultList: null})

    if (itemDesc !== '') {
      //read item name
      if (/Rarity: .*\n.*\n*/.test(itemDesc)) {
        itemName = itemDesc.match(/Rarity: .*\n.*\n/)[0]
        itemType = itemDesc.match(/Rarity: .*\n.*\n.*\n/)[0]
      } else {
        this.clearItem()
        return
      }

      if (!itemName.includes('Unique')) {
        this.clearItem()
        return
      }

      itemName = itemName.split('\n')[1]
      itemType = itemType.split('\n')[2]

      //read item mods
      itemMods = itemDesc.split('\n')
      let startIndex, endIndex

      for (let i = itemMods.length - 1, countLines = 0; i >= 0; i--) {
        if (/--------/.test(itemMods[i])) {
          countLines++
        }

        if (countLines === 2) {
          startIndex = i + 1
          break

        }
      }

      for (let i = itemMods.length - 1, countLines = 0; i >= 0; i--) {
        if (/--------/.test(itemMods[i])) {
          countLines++
        }

        if (countLines === 1) {
          endIndex = i
          break
        }
      }

      itemMods = itemMods.slice(startIndex, endIndex)

      let itemBase = itemDesc.match(/^.*\n.*\n.*\n--------\n.*\n/)[0].match(/--------\n.*/)[0].split('\n')[1]

      let filterQuery = []
      const tmpMods = itemMods.map(mod => mod.replace(/[\d*#+%]/g, '').replace(/ {2}/g, ' ').replace(/^ /g, ''))

      for (let i = 0; i < this.state.mods.length; i++) {
        for (let j = 0; j < tmpMods.length; j++) {
          if ((['increased Physical Damage (Local)', 'increased Attack Speed (Local)',
                'Adds to Physical Damage (Local)', 'Adds to Lightning Damage (Local)',
                'Adds to Cold Damage (Local)', 'Adds to Fire Damage (Local)', 'to Accuracy Rating (Local)',
                'chance to Poison on Hit (Local)', 'increased Accuracy Rating (Local)', 'Adds to Chaos Damage (Local)']
                .includes(this.state.mods[i].text.replace(/[#+%]/g, '').replace(/ {2}/g, ' ').replace(/^ /g, ''))
              && this.state.mods[i].text.replace(/[#+%]/g, '').replace(/ {2}/g, ' ').replace(/^ /g, '') === tmpMods[j] + ' (Local)'
              && ['Bow'].includes(itemBase))
            || this.state.mods[i].text.replace(/[#+%]/g, '').replace(/ {2}/g, ' ').replace(/^ /g, '') === tmpMods[j]) {
            filterQuery = [...filterQuery, this.state.mods[i]]
            tmpMods.splice(tmpMods.indexOf(tmpMods[j]), 1)
            break
          }
        }
      }

      console.log(filterQuery)

      let filterMods = ``

      for (let i = 0; i < filterQuery.length; i++) {
        filterMods += `%7B"id":"${filterQuery[i].id}","value":%7B"min":1%7D%7D`
        if (i !== filterQuery.length - 1)
          filterMods += ','
      }

      //fetch(`https://cors-anywhere.herokuapp.com/http://www.pathofexile.com/api/trade/search/Bestiary?source=%7B"query":%7B"status":%7B"option":"online"%7D,"name":"${itemName}","type":"${itemType}","stats":%5B%7B"type":"and","filters":%5B%7B"id":"explicit.stat_3261801346","value":%7B"min":1%7D%7D%5D%7D%5D%7D,"sort":%7B"price":"asc"%7D%7D`, {method: 'GET'})
      fetch(`https://cors-anywhere.herokuapp.com/http://www.pathofexile.com/api/trade/search/Bestiary?source=%7B"query":%7B"status":%7B"option":"online"%7D,"name":"${itemName}","type":"${itemType}","stats":%5B%7B"type":"and","filters":%5B${filterMods}%5D%7D%5D%7D,"sort":%7B"price":"asc"%7D%7D`, {method: 'GET'})
        .then(response => response.json())
        .then(response => {
            if (response.error) {
              this.setState({
                ...this.state,
                errorList: response.error,
              })
            } else {
              if (response.total > 0)
                for (let i = 0; i < 10; i++) {
                  fetch(`https://www.pathofexile.com/api/trade/fetch/${response.result.slice(i * 10, (i + 1) * 10).join()}?query=${response.id}`, {method: 'GET'})
                    .then(response => response.json())
                    .then(response => {
                      if (!this.state.resultList)
                        this.setState({
                          ...this.state,
                          resultList: response.result,
                        })
                      else
                        this.setState({
                          ...this.state,
                          resultList: [...this.state.resultList, ...response.result],
                        })
                    })
                }
              else
                this.setState({...this.state, loadingList: true, resultList: []})
            }

            this.setState({...this.state, loadingList: false})
          },
        )
    }
  }

  clearItem = () => {
    const state = this.state
    state.itemDesc = ''
    state.resultList = null
    state.loadingList = false
    state.errorList = null
    state.activeItemId = ''
    this.setState(state)
  }

  render () {
    const {itemDesc, resultList} = this.state

    return (
      <div className='container-fluid'>
        <nav className='navbar navbar-expand-lg navbar-light bg-dark'>
          <a className='navbar-brand' style={{color: 'white'}}>Poe unique price</a>
        </nav>
        <br/><br/><br/>
        <div className='row text-center'>
          <div className='col-lg-6'>
            <form onSubmit={this.onSubmit}>
              <div className='form-group'>
                <select className='custom-select mb-2 mr-sm-2 mb-sm-0' name='league' defaultValue='Bestiary'
                        onChange={this.onChange}>
                  <option value='Bestiary'>Bestiary</option>
                  <option value='Standard'>Standard</option>
                </select>
                <br/>
                <label>Paste the unique item below you want to price check</label>
                <button type='button' className='btn-sm btn-info' data-toggle='modal' data-target='#help'>
                  Help
                </button>
                <div className='modal fade' id='help' role='dialog'
                     aria-labelledby='helpLabel' aria-hidden='true'>
                  <div className='modal-dialog' role='document'>
                    <div className='modal-content'>
                      <div className='modal-header'>
                        <h5 className='modal-title' id='helpLabel'>Example description</h5>
                        <button type='button' className='close' data-dismiss='modal' aria-label='Close'>
                          <span aria-hidden='true'>&times;</span>
                        </button>
                      </div>
                      <div className='modal-body'>
                        Rarity: Unique<br/>
                        Chin Sol<br/>
                        Assassin Bow<br/>
                        --------<br/>
                        Bow<br/>
                        Quality: +20% (augmented)<br/>
                        Physical Damage: 86-255 (augmented)<br/>
                        Elemental Damage: 25-50 (augmented)<br/>
                        Critical Strike Chance: 6.50%<br/>
                        Attacks per Second: 1.39 (augmented)<br/>
                        --------<br/>
                        Requirements:<br/>
                        Level: 62<br/>
                        Dex: 212<br/>
                        --------<br/>
                        Sockets: G-G-R-G<br/>
                        --------<br/>
                        Item Level: 72<br/>
                        --------<br/>
                        167% increased Physical Damage<br/>
                        +20 to Dexterity<br/>
                        Adds 25 to 50 Fire Damage<br/>
                        11% increased Attack Speed<br/>
                        100% More Damage with Arrow Hits at Close Range<br/>
                        Bow Knockback at Close Range<br/>
                        --------<br/>
                        As a soldier you want to get close to enemy archers.<br/>
                        That is not the case when fighting the Maraketh.<br/>
                      </div>
                    </div>
                  </div>
                </div>
                <TextareaAutosize className='form-control' style={{minHeight: 100, fontSize: 12}} name='itemDesc'
                                  placeholder='Item description' value={itemDesc} onChange={this.onChange}/>
              </div>
              <button type='submit' className='btn btn-outline-success'>Check price</button>
            </form>
            <br/>
            <button className='btn-sm btn-outline-danger' onClick={this.clearItem}
                    disabled={this.state.loadingList}>Clear
            </button>
          </div>
          <div className='col-lg-6'>
            <h2>Estimated price: </h2>
            <br/>
            {this.mapResultList(resultList)}
          </div>
        </div>
      </div>
    )
  }
}

export default App



